console.log('hi');
let html = document.querySelector('#html');
let style = document.querySelector('#style');
let n = 0;
let string = `/*你好，我叫朱海鳞，
接下来我演示一下我的前端功底，
首先我要准备一个div
*/
#div1{
    border:1px solid red;
    width:200px;
    height:200px;
}
/*接下来我把div变成一个八卦图
注意看好了
首先，把div变成一个圆
*/
#div1{
    border-radius:50%;
    box-shadow:0 0 3px rgba(0,0,0,0.5);
    border:none;
}
/*八卦是阴阳形成的
一黑一白*/
#div1{
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/*加两个神秘的小球*/
#div1::before{
    width:100px;
    height:100px;
    top:0;
    left:50%;
    transform:translateX(-50%);
    background:#000;
    border-radius:50%; 
    background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%);   
}
#div1::after{
    width:100px;
    height:100px;
    bottom:0;
    left:50%;
    transform:translateX(-50%);
    background:#fff;
    border-radius:50%;  
    background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%);  
}

`
console.log(string.length);
let string2 = ``; //缓存要显示的字符串
let step = () => {
    setTimeout(() => {
        //如果是回车，换成<br>,如果是空格，就换成&nbsp,其余照搬
        if (string[n] === '\n') {
            string2 += '<br>'
        } else if (string[n] === ' ') {
            string2 += '&nbsp'
        } else {
            string2 += string[n];
        }
        html.innerHTML = string2;
        style.innerHTML = string.substring(0, n);
        //自动向下滚
        window.scrollTo(0, 9999);
        html.scrollTo(0, 9999)
            //length 52 n 51
        if (++n < string.length) {
            step();
        } else {
            //n是最后一个
        }

    }, 50);
};
//调用递归函数
step();